.list {
  list-style: none;
  margin: 0 auto 20px;
  padding-left: 0;

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32px;

  max-width: 1020px;
}

.buttonscontainer {
  display: flex;
  flex-direction: column;
  justify-content: baseline;
  max-width: 1020px;
  margin: 0 auto 30px auto;
}

.button {
  display: block;
  height: 50px;
  width: 200px;

  max-width: 208px;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  letter-spacing: 0.15px;
  color: #32c625;

  padding: 4px;
  border: 1px solid #32c625;
  border-radius: 4px;
  margin: 0 auto;
  background-color: rgba(33, 191, 186, 0.208);
}

.button:hover,
.button:focus {
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2), 0px 3px 4px rgba(0, 0, 0, 0.12), 0px 2px 4px rgba(0, 0, 0, 0.14);
}

.disabled {
 display: block;
  height: 50px;
  width: 200px;

  max-width: 208px;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  letter-spacing: 0.15px;
  color: #585958e1;

  padding: 4px;
  border: 1px solid #585958e1;
  border-radius: 4px;
  margin: 0 auto;
  background-color: rgba(190, 198, 197, 0.12);
}

.pageTitle {
  display: inline-block;
  margin: 0 auto;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  letter-spacing: 0.15px;
  color: #0a99e0;
}

@media screen and (min-width: 768px) {
  .list {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    row-gap: 24px;
    column-gap: 20px;
  }

  .buttonscontainer {
  display: flex;
  flex-direction: row;
  align-items: baseline;
}
}